@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}body{background-color:#ecf0f1;font-family:'Roboto', sans-serif;margin:0;min-height:100vh;display:flex;align-items:center;justify-content:center}.calculator{background-color:#474747;box-shadow:2px 2px 5px rgba(0,0,0,0.5);display:flex;flex-wrap:wrap;padding:5px;height:390px;width:250px;border-radius:15px}.display{border-radius:15px;color:#fff;font-size:36px;position:relative;padding:20px 10px;text-align:right;height:70px;width:100%}.nums-container{width:75%}.ops-container{width:25%}button{border:none;border-radius:20%;cursor:pointer;font-size:24px;margin:5px;height:50px;width:50px}button:hover{opacity:0.9}button:active{-webkit-transform:scale(0.9);transform:scale(0.9)}button:focus{outline:none}button.big-v{border-radius:50px;height:110px}button.big-h{border-radius:10px;width:110px}button.gray{background-color:#818181;color:#fff}button.dark-grey{background-color:#323232;color:#fff}button.ac{margin-right:50%}.tomato{background-color:#eb8371}

